    // Пример асинхронной функции для парсинга ответа от toEisSendPlan
    const parseEisSendResponse = async (response: any): Promise<string> => {
        // Получаем значение result из вложенной структуры ответа
        const result = response?.message?.text?.text?.body?.result

        if (result === 'success') {
            return 'Всё прошло успешно'
        } else if (result === 'failure') {
            const violations =
                response?.message?.text?.text?.body?.violations?.violation
            const errorDescriptions: string[] = []

            // Регулярное выражение для поиска GUID (регистр не важен)
            const guidRegex =
                /[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}/gi

            // Функция для обработки одного объекта нарушения
            const processViolation = async (v: any) => {
                if (v.level === 'error' && v.description) {
                    // Ищем GUID в тексте ошибки
                    const guidMatches = v.description.match(guidRegex)

                    if (guidMatches && guidMatches.length > 0) {
                        // Если найдено несколько GUID – для каждого формируем отдельное сообщение
                        for (const guid of guidMatches) {
                            try {
                                // Вызываем функцию для получения данных по позиции
                                const positionData =
                                    await positionDraftGuid(guid)
                                const contractSubject =
                                    positionData.contract_subject
                                // Убираем GUID из исходного текста (чтобы не дублировать его)
                                const cleanedDescription = v.description
                                    .replace(guid, '')
                                    .trim()
                                // Формируем сообщение согласно требуемому формату
                                const formattedError = `Позиция: ${contractSubject}\nПо позиции ${guid} ${cleanedDescription}`
                                errorDescriptions.push(formattedError)
                            } catch (err) {
                                console.error(
                                    'Ошибка при получении данных позиции для GUID:',
                                    guid,
                                    err
                                )
                                // Если ошибка при запросе, добавляем исходное описание
                                errorDescriptions.push(v.description)
                            }
                        }
                    } else {
                        // Если GUID не найден, просто добавляем исходное описание ошибки
                        errorDescriptions.push(v.description)
                    }
                }
            }

            // Если violations – это массив, обрабатываем каждый элемент
            if (Array.isArray(violations)) {
                for (const violation of violations) {
                    await processViolation(violation)
                }
            } else if (violations && violations.level === 'error') {
                // Если violation – это одиночный объект
                await processViolation(violations)
            }

            return errorDescriptions.join('\n\n')
        }

        return 'Неизвестный формат ответа'
    }

    const toEisSendPlan = async (
        planGuid: string,
        send_info: ToEisSendInfo
    ): Promise<void> => {
        const url =
            process.env.REACT_APP_API_URL +
            `/api/backend/v1/plan_to_eis?plan_guid=${planGuid}`

        try {
            const response = await fetch(url, {
                method: 'POST',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(send_info),
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to send plan to EIS from. Status: ${response.status}`
                )
            }

            const responseData = await response.json()
            const parsedResponse = await parseEisSendResponse(responseData)

            setToEisSendResponse(parsedResponse)
            setToEisSendResponsePopup(true)
            console.log('Position send plan to EIS successfully')
        } catch (error) {
            console.error('Error while sending plan to EIS:', error)
            setToEisSendResponse(`Ошибка при отправке плана в ЕИС:\n- ${error}`)
            setToEisSendResponsePopup(true)
        } finally {
            loadPlan()
        }
    }

    const updatePlanFromEis = async (reg_number: number): Promise<void> => {
        const url =
            process.env.REACT_APP_API_URL + `/api/backend/v1/plan_from_eis`
        setUpdatingFromEis(true)

        try {
            const response = await fetch(url, {
                method: 'PUT',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ reg_number: reg_number }),
            })

            if (response.ok) {
                const responseData = await response.json()

                // Обработка ошибки одинаковых весрий
                if (
                    responseData.details &&
                    responseData.details.includes('version')
                ) {
                    setPopupMessage(
                        'Версия плана в базе и версия плана в ЕИС одинакова. Хотите продолжить обновление?'
                    )
                    setForceUpdateUrl(
                        process.env.REACT_APP_API_URL +
                            `/api/backend/v1/plan_from_eis?force_new_plan=true&force_update=true`
                    )
                    setUpdateFromEisPopup(true)
                    return
                }

                // Обработка ошибки несовпадения данных
                if (
                    responseData.details &&
                    responseData.details.includes("'is_success': False")
                ) {
                    setPopupMessage(
                        'Данные из ЕИС отличаются от данных в базе. Хотите продолжить обновление?'
                    )
                    setForceUpdateUrl(
                        process.env.REACT_APP_API_URL +
                            `/api/backend/v1/plan_from_eis?force_update=true`
                    )
                    setUpdateFromEisPopup(true)
                    return
                }

                // Обработка c отличием ИНН
                if (
                    responseData.details &&
                    responseData.details.includes('ИНН')
                ) {
                    setPopupMessage(
                        'ИНН плана закупки не соответствует ИНН вашей организации'
                    )
                    return
                }

                // Обработка c отличием ИНН
                if (
                    responseData.details &&
                    responseData.details.includes('multiple plan')
                ) {
                    setPopupMessage(
                        'Невозможно добавить план, так как он уже имеется в базе'
                    )
                    return
                }

                // Обработка успешного ответа
                const newGuid = responseData.plan?.new_guid

                if (newGuid) {
                    setPlanGuidNav(newGuid) // Обновите состояние
                    navigate(`/hub/plans/${newGuid}/positions`)
                    setTakeFromEisPopup(false)
                }
            }

            if (!response.ok) {
                let errorData
                try {
                    errorData = await response.json()
                } catch (e) {
                    console.error('Failed to parse JSON from response:', e)
                    throw new Error(
                        `Unexpected error: Unable to parse JSON. Status: ${response.status}`
                    )
                }
                // Если ошибка не подходит под указанные типы
                throw new Error(
                    `Failed to update plan from EIS. Status: ${response.status}, Message: ${errorData.message}`
                )
            }
        } catch (error) {
            console.error('Error while updating plan from EIS:', error)
            setPopupMessage('Ошибка связывания плана. Проверте номер плана')
        } finally {
            setUpdatingFromEis(false) // Отключаем анимацию загрузки
            setTakingFromEis(false) // Отключаем анимацию загрузки
            loadPlan()
        }
    }

